<template>
  <div>
    <div class="card1">
      <el-table
        :data="studentList"
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop="u_name"
          label="学号"
        />
        <el-table-column
          prop="u_rname"
          label="姓名"
        />
        <el-table-column
          prop="u_m_tel"
          label="专业"
        />
        <el-table-column
          prop="u_fixed_tel"
          label="班级"
        />
        <el-table-column
          prop="u_tel"
          label="电话"
        />
        <el-table-column label="操作">
          <div slot-scope="scope">
            <el-button @click="viewTestpaper(scope.row)">
              查看试卷
            </el-button>
          </div>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible1"
      width="80%"
      :before-close="handleClose1"
    >
      <el-table
        :data="testPaperList"
        stripe
        style="width: 100%"
      >
        <el-table-column
        type=index
          label="序号"
        />
        <el-table-column
          prop="stu_test.tst_test_id.tt_name"
          label="试卷"
        />
        <el-table-column
          prop="stu_test.tst_teacher.u_rname"
          label="出题老师"
        />
        <el-table-column
          prop="test_st"
          label="成绩"
        />
        <el-table-column
          prop="reach"
          label="课程目标达成度"
        />
        <el-table-column label="操作">
          <div slot-scope="scope">
            <el-button @click="viewQuestion(scope.row)">
              查看详细
            </el-button>
          </div>
        </el-table-column>
      </el-table>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible1 = false">取 消</el-button>
        <el-button
          type="primary"
          @click="dialogVisible1 = false"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible2"
      width="80%"
      :before-close="handleClose2"
    >
      <el-table
        :data="questionList"
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop="ttss_title"
          label="题目"
        />
        <el-table-column
          prop="ttss_base_score"
          label="分值"
        />
        <el-table-column
          prop="ttss_get_score"
          label="得分"
        />
      </el-table>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible2 = false">取 消</el-button>
        <el-button
          type="primary"
          @click="dialogVisible2 = false"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      studentList: [],
      testPaperList: [],
      questionList: []
    }
  },
  created () {
    this.getstudentList()
  },
  methods: {
    async getstudentList () {
      const { data: res } = await this.$http.get('admin/showStu')
      this.studentList = res.obj
    },
    async viewTestpaper (row) {
      this.dialogVisible1 = true
      const u_id = row.u_id 
      const { data: res } = await this.$http.get('test/showTestById', { params: { u_id } })
      this.testPaperList = res.obj
      console.log(res)
    },
    viewQuestion (row) {
      this.dialogVisible2 = true
      this.questionList = row.test_stu_scores
      console.log(this.questionList)
    },
    handleClose1 () {
      this.dialogVisible1 = false
      
    },
    handleClose2(){
      this.dialogVisible2 = false
    }
  }
}
</script>
