<template>
  <div>
    <div class="card1">
      <el-table
        :data="userlist"
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop="u_id"
          label="ID"
          width="180"
        />
        <el-table-column
          prop="u_name"
          label="用户名"
          width="180"
        />
        <el-table-column
          prop="u_tel"
          label="手机"
        />
        <el-table-column
          prop="u_email"
          label="邮箱"
        />
        <el-table-column
          prop="role_id.role_Name"
          label="角色"
        />
        <el-table-column label="操作">
          <div slot-scope="scope">
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="setRightDialog(scope.row)"
            />
          </div>
        </el-table-column>
      </el-table>
      <div class="paginationBox">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        />
      </div>
      <el-dialog
        width="30%"
        title="设置角色权限"
        top="6vh"
        v-if="isvisible"
        :visible="isvisible"
        @close="setrightclose"
        :show-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
      >
        <el-form
          :model="alterForm"
          ref="alterFormref"
          label-width="95px"
          class="demo-ruleForm"
        >
          <el-form-item label="用户">
            <el-input v-model="alterForm.u_name" />
          </el-form-item>

          <el-form-item label="权限">
            <el-select
              v-model="alterForm.role_id.role_Id"
              placeholder="请选择"
            >
              <el-option
                label="教务员"
                value="1"
              />
              <el-option
                label="老师"
                value="2"
              />
              <el-option
                label="学生"
                value="3"
              />
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button
              type="primary"
              @click="submitForm('alterFormref')"
            >
              确认
            </el-button>

            <el-button @click="resetForm('alterFormref')">
              重置
            </el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isvisible: false,
      alterForm: [],
      userlist: [],
      currentPage: 1,
      pageSize: 10,
      total: 0
    }
  },
  created () {
    this.getuserlist()
    this.getpag(this.currentPage, this.pageSize)
  },
  methods: {
    async getuserlist () {
      const { data: res } = await this.$http.get('admin/showAll')
      this.userlist = res.obj.list
    },
    setRightDialog (row) {
      this.isvisible = true
      this.alterForm = row
      this.alterForm.role_id.role_Id = this.alterForm.role_id.role_Id + ''
    },
    setrightclose () {
      this.isvisible = false
    },
    async submitForm (ref) {
      const u_id = this.alterForm.role_id.role_Id
      const role_Id = this.alterForm.role_id.role_Id
      const { data: res } = await this.$http.post('admin/changePower', { u_id, role_Id })
      this.getuserlist()
      this.setrightclose()
    },
    async getpag (page, num, list = false) {
      const { data: res } = await this.$http.post('admin/page', { page, num })
      this.total = res.obj.total
      if (list) {
        this.userlist = res.obj.list
      }
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.getpag(this.currentPage, this.pageSize, true)
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getpag(this.currentPage, this.pageSize, true)
    }
  }
}
</script>
<style lang="less" scoped>
.el-pagination {
  margin: 0 auto;
  width: 530px;
}
</style>
