<template>
  <div>
    <el-tabs
      v-model="activename"
      @tab-click="handleClick"
      tab-position="left"
    >
      <el-tab-pane
        label="查看状态"
        name="status"
      >
        <div>
          <p>标准要求：</p>
          <p>1.与本专业毕业要求相适应的数学与自然科学类课程（至少占总学分的15%）。</p>
          <p>
            2.符合本专业毕业要求的工程基础类课程、专业基础类课程与专业类课程（至少占总学分的30%）。工程基础类课程和专业基础类课程能体现数学和自然科学在本专业应用能力培养，专业类课程能体现系统设计和实现能力的培养。
          </p>
          <p>
            3.工程实践与毕业设计（论文）（至少占总学分的20%）。设置完善的实践教学体系，并与企业合作，开展实习、实训，培养学生的实践能力和创新能力。毕业设计（论文）选题要结合本专业的工程实际问题，培养学生的工程意识、协作精神以及综合应用所学知识解决实际问题的能力。对毕业设计（论文）的指导和考核有企业或行业专家参与。
          </p>
          <p>
            4.人文社会科学类通识教育课程（至少占总学分的15%），使学生在从事工程设计时能够考虑经济、环境、法律、伦理等各种制约因素。
          </p>
          <el-table
            :data="statusTabledata"
            border
            style="width: 100%"
          >
            <el-table-column
              prop="name"
              width="250"
              label="材料名称"
            >
              <div slot-scope="scope">
                <router-link to="">
                  {{ scope.row.name }}
                </router-link>
              </div>
            </el-table-column>

            <el-table-column
              width="180"
              prop="type"
              label="类型"
            />
            <el-table-column
              width="180"
              prop="status"
              label="状态"
            />
            <el-table-column
              prop="tips"
              label="备注"
            />
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        ref="addCourseref"
        name="addCourse"
      >
        <span slot="label">{{ addCourselabel }}</span>
        <el-form
          label-width="90px"
          :rules="rules"
          :inline="true"
          ref="courseFormref"
          :model="courseForm"
        >
          <el-form-item
            label="课程名称"
            prop="tc_name"
          >
            <el-input v-model="courseForm.tc_name" />
          </el-form-item>
          <el-form-item label="课程ID">
            <el-input v-model="courseForm.tc_showid" />
          </el-form-item>
          <el-form-item
            label="责任教师"
            prop="teach_ids"
          >
            <el-select
              v-model="courseForm.teach_ids"
              placeholder="请选择"
            >
              <el-option
                v-for="item in teacheroptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item
            label="学分"
            prop="tc_score"
          >
            <el-input
              type="number"
              min="0"
              v-model="courseForm.tc_score"
            />
          </el-form-item>
          <el-form-item
            class="texts"
            label="课程简介"
          >
            <editor
              id="editor"
              :catch-data="catchData"
              :content="courseForm.tc_describe"
            />
          </el-form-item>
          <el-form-item label="考核方法">
            <div class="form2">
              <el-form-item label="平时成绩(%)">
                <el-input v-model="courseForm.tc_usual_performance" />
              </el-form-item>
              <el-form-item label="报告成绩(%)">
                <el-input v-model="courseForm.tc_report_results" />
              </el-form-item>
              <el-form-item label="实验成绩(%)">
                <el-input v-model="courseForm.tc_experimental_results" />
              </el-form-item>
              <el-form-item label="实训成绩(%)">
                <el-input v-model="courseForm.tc_training_results" />
              </el-form-item>
              <el-form-item label="期中成绩(%)">
                <el-input v-model="courseForm.tc_interim_results" />
              </el-form-item>
              <el-form-item label="课外成绩(%)">
                <el-input v-model="courseForm.tc_extracurricular_performance" />
              </el-form-item>
              <el-form-item label="期末成绩(%)">
                <el-input v-model="courseForm.tc_final_exam" />
              </el-form-item>
              <el-tag type="warning">
                注：总评成绩是不同类型的成绩按权重求和而得到的。
              </el-tag>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="formbtn">
              <el-button @click="submitForm('courseFormref')">
                提交
              </el-button>
              <el-button @click="closeForm()">
                取消
              </el-button>
            </div>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane
        name="coures"
        label="查看课程"
      >
        <div>
          <el-table
            :data="courselist"
            stripe
            style="width: 100%"
          >
            <el-table-column
              prop="tc_name"
              label="课程"
              width="180"
            />
            <el-table-column
              prop="tc_showid"
              label="课程ID"
              width="180"
            />
            <el-table-column
              prop="tc_score"
              label="学分"
            />
            <el-table-column label="操作">
              <div slot-scope="scope">
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="delcourse(scope.row)"
                />
                <el-button
                  type="warning"
                  size="mini"
                  icon="el-icon-edit"
                  @click="alter(scope.row)"
                />
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-share"
                  @click="setknowledgepoint(scope.row)"
                />
              </div>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        name="help"
        label="帮助"
      >
        <h3>创建课程</h3>
        <p>你可以在创建课程页面创建新的课程，其中包括一些必要的课程信息</p>
        <h3>查看课程</h3>
        <p>你可以查看所有的课程，并编辑\删除已有的课程</p>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      title="知识点"
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      width="80%"
    >
      <div />
      <div class="card1">
        <span>第一章</span>
        <el-collapse @change="handleChange">
          <el-collapse-item>
            <template
              slot="title"
              class="card-style"
            >
              <i class="iconfont icon-jiahao" />
              <div class="kPitem">
                <el-badge :value="12">
                  <span>知识点</span>
                </el-badge>
                <div class="itembtn">
                  <el-button
                    type="warning"
                    size="mini"
                    icon="el-icon-edit"
                    @click.stop="editkp"
                  >
                    编辑
                  </el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    @click.stop=""
                  >
                    删除
                  </el-button>
                </div>
              </div>
            </template>
            <el-card class="kPbody">
              <p>标题</p>
              <div class="tableheader">
                指标点
              </div>
              <div>
                <div class="tabletitle">
                  123
                </div>
                <div class="tableitem">
                  123
                </div>
              </div>
            </el-card>
          </el-collapse-item>
        </el-collapse>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible1 = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="添加知识点"
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      width="80%"
    >
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible2 = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import editor from './component/editor'
export default {
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      addCourselabel: '创建课程',
      isalter: false,
      activename: 'status',
      courseForm: {
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      },
      courselist: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      teacheroptions: [],
      statusTabledata: [
        { name: '基本情况', type: '必须', status: '准备中', tips: '课程基本情况' },
        { name: '支持矩阵', type: '必须', status: '完成', tips: '	专业主干课对本专业要求的支持矩阵图' },
        { name: '毕业设计', type: '必须', status: '准备中', tips: '	学生毕业设计（论文）' }
      ],
      rules: {
        tc_name: [{ required: true, message: '请输入课程名称', trigger: 'blur' }],
        tc_id: [{ required: true, message: '请输入课程ID', trigger: 'blur' }],
        tc_teacher: [{ required: true, message: '请输入责任教师', trigger: 'blur' }],
        tc_score: [{ required: true, message: '请输入学分', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getcourselist()
    this.getteacherlist()
  },
  mounted () {},
  methods: {
    handleClick (tab, e) {
      this.$emit('getpath1', tab.label || this.addCourselabel)
      if (this.activename != 'addCourse') {
        this.addCourselabel = '创建课程'
        this.isalter = false
        // 初始化表单
        this.courseForm = {
          tc_describe: '',
          tc_usual_performance: 0,
          tc_experimental_results: 0,
          tc_interim_results: 0,
          tc_final_exam: 100,
          tc_report_results: 0,
          tc_training_results: 0,
          tc_extracurricular_performance: 0
        }
      }
    },
    async getcourselist () {
      const { data: res } = await this.$http.get('course/showAll')
      this.courselist = res.obj.list
    },
    async getteacherlist () {
      const { data: res } = await this.$http.get('admin/showTeacher')
      res.obj.some(item => {
        const obj = { label: item.u_name, value: item.u_id }
        this.teacheroptions.push(obj)
      })
    },

    // 知识点
    async setknowledgepoint (row) {
      this.dialogVisible1 = true
      const tc_id = row.tc_id
      const { data: res } = await this.$http.post('kpc/cid', { tc_id })
      console.log(res)
    },
    // 切换编辑课程
    alter (row) {
      this.addCourselabel = '编辑课程'
      this.$emit('getpath1', this.addCourselabel)
      this.activename = 'addCourse'
      this.isalter = true
      row.teach_ids = row.tc_teacher.u_id
      delete row.tc_teacher
      this.courseForm = row
    },
    // 删除课程
    async delcourse (row) {
      console.log(row.tc_id)
      const tc_id = row.tc_id
      // 弹窗询问是否删除
      const confirmres = await this.$confirm('是否确认删除该课程?', '确认删除', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.post('course/del', { tc_id })
      console.log(res)
      this.getcourselist()
    },
    // 关闭编辑课程
    closeForm () {
      this.addCourselabel = '创建课程'
      this.$emit('getpath1', this.addCourselabel)
      this.activename = 'coures'
      this.isalter = false
      // 初始化表单
      this.courseForm = {
        tc_describe: '',
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      }
    },
    // 提交表单
    submitForm (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (!isalter) {
          const { data: res } = await this.$http.post('course/add', this.courseForm)
          return false
        }
        const { data: res } = await this.$http.post('course/update', this.courseForm)
      })
    },
    catchData () {},
    editkp () {
      this.dialogVisible2 = true
    },
    async delkp (tkp_id) {
      const { data: res } = await this.$http.post('kpc/del', { tkp_id })
      console.log(res)
    },
    handleClose1 () {
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.dialogVisible2 = false
    },
    handleChange () {
      setTimeout(() => {
        const right = document.getElementsByClassName('el-collapse-item__arrow el-icon-arrow-right')
        right.forEach(element => {
          if (element.className === 'el-collapse-item__arrow el-icon-arrow-right is-active') {
            element.previousSibling.previousSibling.className = 'iconfont icon-jianhao'
          } else {
            element.previousSibling.previousSibling.className = 'iconfont icon-jiahao'
          }
        })
      }, 50)
    }
  },
  components: {
    editor
  }
}
</script>
<style lang="less" scoped>
.el-form {
  padding: 0 120px;
  > .el-form-item:nth-child(-n + 4) {
    margin-right: 175px;
  }
}
.form2 {
  width: 839px;
  border: 1px solid #e1e1e1;
  padding: 20px;
  .el-form-item {
    margin-right: 105px;
  }
}
.el-form > .el-form-item:last-child {
  width: 100%;
  text-align: center;
}
.formbtn {
  width: 250px;
  display: flex;
  justify-content: space-between;
}
#editor {
  width: 839px;
  position: relative;
  z-index: 0;
}
.el-select {
  width: 202px;
}
.card1 {
  display: flex;
  justify-content: space-between;
  span {
    line-height: 28px;
  }
}
.el-collapse {
  border: none;
  width: 85%;
}
.kPitem {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.kPbody {
  p {
    padding-left: 5px;
    border-left: 2px solid #337ab7;
  }
}
.tableheader {
  border: 1px solid #e1e1e1;
  border-radius: 4px 4px 0 0;
  padding: 8px;
}
.el-card {
  margin-top: 10px;
}
.el-collapse-item {
  margin-bottom: 10px;
}
.tabletitle,
.tableitem {
  border: 1px solid #e1e1e1;
  padding: 10px;
}
.tabletitle {
  height: 40px;
  background-color: #f5f7fa;
}
.tableitem {
}
</style>
